/* stylelint-enable */
@import 'https://fonts.googleapis.com/css?family=Poiret+One|Titillium+Web';
[data-theme='formal'] body {
  font-family: "TimesNewRoman", "Times New Roman", "Times", "Baskerville", serif;
}

[data-theme='modern'] body {
  font-family: "Titillium Web", sans-serif;
}

@media screen {
  [data-theme='yotsuba-b'] body {
    background: #eef2ff url("data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAEAAADICAIAAACmkByiAAAATklEQVQokWO6ePUdEwMDA1bMyIguxkikOlxqsOvHbwYjXvNR5RiJchMut2DTw8hIrJ8Z8ZiBPfxwuRGbnQNvH33jgph0hStuiE1XRKgDAPUXBHCAyYNuAAAAAElFTkSuQmCC") repeat-x 50% 0%;
    color: black;
    font-family: "Helvetica Neue", "Helvetica", "Calibri", "Arial", sans-serif;
  }
}

[data-theme='formal'] h1, [data-theme='formal']
h2, [data-theme='formal']
h3, [data-theme='formal']
h4, [data-theme='formal']
h5, [data-theme='formal']
h6 {
  font-weight: bold;
}

[data-theme='modern'] h1, [data-theme='modern']
h2, [data-theme='modern']
h3, [data-theme='modern']
h4, [data-theme='modern']
h5, [data-theme='modern']
h6 {
  font-family: "Poiret One", sans-serif;
  font-weight: bold;
  letter-spacing: 2px;
}

@media screen {
  [data-theme='yotsuba-b'] h1, [data-theme='yotsuba-b']
  h2, [data-theme='yotsuba-b']
  h3, [data-theme='yotsuba-b']
  h4, [data-theme='yotsuba-b']
  h5, [data-theme='yotsuba-b']
  h6 {
    font-weight: bold;
  }
}

@media screen {
  [data-theme='yotsuba-b'] h1, [data-theme='yotsuba-b']
  h2 {
    color: #af0a0f;
  }
}

@media screen {
  [data-theme='yotsuba-b'] h3, [data-theme='yotsuba-b']
  h4 {
    color: #0f0c5d;
  }
}

[data-theme='modern'] h1 small, [data-theme='modern']
h2 small, [data-theme='modern']
h3 small, [data-theme='modern']
h4 small, [data-theme='modern']
h5 small, [data-theme='modern']
h6 small {
  font-family: "Titillium Web", sans-serif;
  letter-spacing: 1px;
}

@media screen {
  [data-theme='yotsuba-b'] h1 small, [data-theme='yotsuba-b']
  h2 small, [data-theme='yotsuba-b']
  h3 small, [data-theme='yotsuba-b']
  h4 small, [data-theme='yotsuba-b']
  h5 small, [data-theme='yotsuba-b']
  h6 small {
    color: #0f0c5d;
  }
}

[data-theme='modern'] a {
  color: #6795ce;
}

@media screen {
  [data-theme='yotsuba-b'] a {
    color: #34345c;
  }
}

@media screen {
  [data-theme='yotsuba-b'] hr {
    border-top: 1px solid #b7c5d9;
  }
}

@media screen {
  [data-theme='modern'] code {
    border: none;
  }
}

@media screen {
  [data-theme='yotsuba-b'] blockquote {
    border: none;
    color: #789922;
    font-size: 1.1em;
    padding: none;
  }
}
